import React,{useState, useEffect, useContext} from 'react'
import {useCallback} from 'react'
const url = 'https://www.thecocktaildb.com/api/json/v1/1/search.php?s='
const AppContext = React.createContext()

const AppProvider = ({children}) =>{
    const [loading, setLoading]= useState(false);
    const [searchTerm, setSearchTerm] = useState('a');
    const [coacktails, setCoacktails ] = useState([]);

    const fetchDrinks =useCallback(async () =>{
        setLoading(true);
        try{
            const response = await fetch(`${url}${searchTerm}`)
            const data = await response.json();
            const {drinks} = data;
            if(drinks){
                const newCocktails = drinks.map((item)=>{
                    const {idDrink,strDrink,strDrinkThumb, strAlcoholic, strGlass} = item;
                    return{id:idDrink, name:strDrink, img:strDrinkThumb, info:strAlcoholic, glass:strGlass} 
                })
                setCoacktails(newCocktails)
            }else{
                setCoacktails([]);
            }
            setLoading(false)
            // console.log(data);
        }catch (error) {
            // console.log(error)
            setLoading(false)
        }
    },[searchTerm])
    useEffect(()=>{
        fetchDrinks()
    },[searchTerm, fetchDrinks])

    return(
        <AppContext.Provider value={{loading, coacktails, setSearchTerm}}>
        {children}
        </AppContext.Provider>
    )
}

//make Use use

export const useGlobleContext = () =>{
    return useContext(AppContext)
}
export {AppContext, AppProvider}